import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import React from "react";
import emailjs from "@emailjs/browser";

import ContactLinks from "../components/contactLinks";
import SectionBreak from "../components/SectionBreak";

const EMAILJS_SERVICE_ID = "service_8s75uxh";

class Contact extends React.Component {
  state = {
    emailSuccess: null
  };

  renderError({ error, touched }) {
    if (touched && error) {
      return (
        <div className="ui error message">
          <div className="header">{error}</div>
        </div>
      );
    }
  }

  renderInput = ({ input, label, meta }) => {
    const className = `field ${meta.error && meta.touched ? "error" : ""}`;
    return (
      <div className={className}>
        <label>{label}</label>
        <input {...input} autoComplete="off" />
        {this.renderError(meta)}
      </div>
    );
  };

  emailMessges = () => {
    switch (this.state.emailSuccess) {
      case true:
        return (
          <div className="ui positive message">
            <div className="header">Success!</div>
            <p>Your message was successfully sent to Abby.</p>
          </div>
        );
      case false:
        return (
          <div className="ui error message">
            <div className="header">Failed :(</div>
            <p>Please try again or email directly.</p>
          </div>
        );

      default:
        return <div></div>;
    }
  };

  onSubmit = (formValues) => {
    const email_message = {
      from_name: formValues.name,
      from_email: formValues.email,
      message: formValues.message
    };

    emailjs
      .send(
        EMAILJS_SERVICE_ID,
        "template_vfkkwij",
        email_message,
        "6uggr0d7aAIxBqzkd"
      )
      .then(
        (result) => {
          this.setState({
            emailSuccess: true
          });
        },
        (error) => {
          this.setState({
            emailSuccess: false
          });
        }
      );
  };

  render() {
    return (
      <div id="Contact">
        <SectionBreak />

        <div className="ui container grid">
          <h2 className="ui header">Contact Me!</h2>
          <div className="ui two column stackable container grid">
            <div className="ui eight wide column">
              <form
                onSubmit={this.props.handleSubmit(this.onSubmit)}
                className="ui form error"
              >
                <div className="nine wide field">
                  <Field
                    name="name"
                    component={this.renderInput}
                    label="Your Full Name"
                  />

                  <Field
                    name="email"
                    component={this.renderInput}
                    label="Your Email"
                  />
                  <Field
                    name="message"
                    component={this.renderInput}
                    label="Your Message"
                  />
                </div>

                {this.emailMessges()}

                <button className="ui button" type="submit">
                  <i className="envelope outline icon"></i>Send!
                </button>
              </form>
            </div>
            <div className="ui column">
              <ContactLinks />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const validate = (formValues) => {
  const errors = {};

  if (!formValues.name) {
    errors.name = "Please enter your name";
  }

  if (!formValues.email) {
    errors.email = "please enter your email";
  }

  if (!formValues.message) {
    errors.message = "please tell me something :)";
  }
  return errors;
};

export default reduxForm({
  form: "contactForm", // can be anyname as long as it reflects the purpose
  validate
})(Contact);
