import React, { useEffect } from "react";
import { Dropdown } from "semantic-ui-react";
const imageURL = require("../images/40651648153116_.pic.jpg");

function LeftMenu() {
  useEffect(() => {
    let url = window.location.href.split("/");
    let target = url[url.length - 1].toLowerCase();
    let element = document.getElementById(target);
    element && element.scrollIntoView({ behavior: "smooth", block: "start" });
  }, []);

  const handleOnclick = (e) => {
    const item = e.target.innerText;
    let scrollItem = document.getElementById(item);
    e.preventDefault();
    scrollItem &&
      scrollItem.scrollIntoView({ behavior: "smooth", block: "start" });
    window.history.pushState(item, item, `/${item}`);
  };

  const projectOptions = [
    { text: "Websites", value: "Websites", onClick: handleOnclick },
    {
      text: "FullStackProjects",
      value: "FullStackProjects",
      onClick: handleOnclick
    },
    { text: "MiniGames", value: "MiniGames", onClick: handleOnclick }
  ];

  return (
    <>
      <a className="active item" onClick={handleOnclick}>
        About
      </a>
      <Dropdown
        className="active item"
        placeholder="Projects"
        options={projectOptions}
      />
      {/* <a className="ui simple dropdown item">
        Projects
        <i className="dropdown icon"></i>
        <div className="menu">
          <div href="/" onClick={handleOnclick} className="link item">
            Websites
          </div>
          <div href="/" onClick={handleOnclick} className="link item">
            FullStackProjects
          </div>
          <div href="/" onClick={handleOnclick} className="link item">
            MiniGames
          </div>
        </div>
      </a> */}
      <a className="active item" onClick={handleOnclick}>
        Contact
      </a>
      <div className="right menu">
        <div className="item" id="About">
          <img className="ui avatar image" src={imageURL} />
          Abby
        </div>
      </div>
    </>
  );
}

export default function Navbar() {
  return (
    <div className="ui massive stackable secondary pointing menu navbar">
      <div className="ui container">
        <LeftMenu />
      </div>
    </div>
  );
}
